/* 
PROSE BLOCK
Default value for spacing between text paragraphs.
*/
.prose {
  --flow-space: var(--space-m);
}

/* 
GLOBAL STYLES
Site-wide styles.
*/
html, body {
  background: red;
}

/* 
REGION UTILITY
Consistent block padding for page sections.
*/
.region-small {
  padding-block: var(--region-space-small, var(--space-m-l));
}

.region-normal {
  padding-block: var(--region-space-normal, var(--space-l-xl));
}

.region {
  padding-block: var(--region-space, var(--space-xl-2xl));
}

.region-bottom {
  padding-block-end: var(--region-space, var(--space-xl-2xl));
}

/* 
FLOW UTILITY
Spacing value between content blocks.
*/
.flow > * + * {
  margin-top: var(--flow-space, 1em);
}

.flow-large > * + * {
  margin-top: var(--flow-space-large, 1em);
}

/* 
WRAPPER UTILITY
Settings for the overall content width.
*/
.wrapper {
  margin-inline: auto;
  max-width: clamp(16rem, var(--wrapper-max-width, 100vw), 80rem);
  padding-left: var(--gutter);
  padding-right: var(--gutter);
  position: relative;
}

/* 
VISUALLY HIDEN UTILITY
Modern way of properly hiding elements.
*/
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 0;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

/* 
BORDER RADIUS UTILITY
Values for consistent border radius.
*/
:root {
  --radius-xs: 3px;
  --radius-small: 8px;
  --radius-normal: 16px;
  --radius-large: 48px;
}

.rounded {
  border-radius: var(--radius-normal);
}